.Bars {
  position: relative;
  display: inline-block;
  left: 20rem;
  top: -10rem;
  font-size: 12px;
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 1rem;
  border-radius: 1rem;
  background: rgba(10, 10, 10, 0.2);
  width: 25rem;
  backdrop-filter: blur(2px);
}
* img {
  cursor: pointer;
}

.com {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0.1rem;
  padding: 0px;
  height: 2rem;
  width: auto;
}
.Web {
  background-color: rgb(57, 172, 143);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.ReactJs {
  background-color: rgb(32, 181, 197);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Java {
  background-color: rgb(199, 90, 90);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Android {
  background-color: #a4c639;
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Flutter {
  background-color: rgb(39, 128, 196);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Git {
  background-color: rgb(208, 107, 13);
  margin: 1rem;
  height: 8px;
  border-radius: 0.5rem;
}
.Social {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Social img {
  margin: 1rem;
  height: 1.5rem;
}

.SocialSite:hover {
  border-radius: 50%;
  /* box-shadow: 0px 00px  10px rgba(0, 247, 255,0.8);   */
  box-shadow: 0px 00px 10px white;
}
@media screen and (max-width: 450px) {
  .Bars {
    left: 3.5rem;
    top: 8rem;
    display: flex;
    width: 18rem;
  }
  .Social {
    display: flex;
    padding: 0.5rem;
    justify-content: space-evenly;
  }
}
